import React from "react";
import axios from "axios";
import { useDispatch, useSelector } from "react-redux";
import { SnackbarProvider, useSnackbar } from "notistack";

import { AddtoCard } from "../../../Actions";
import Show from "../../show";
import Signin from "../../signIn";

function MyApp({ price, sch, product }) {
  const sign = Show();
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const card = useSelector((state) => {
    if (state.user === null) return null;
    const user = state.user;
    return user.card;
  });

  return (
    <>
      <div className="cost">
        <form action="/api/done/payment" method="POST" id="payment">
          <a
            onClick={() => {
              if (card === null) {
                sign.sshow();
                return;
              }
              document.getElementById("payment").submit();
            }}
            className="buy-btn"
          >
            Try Now
          </a>
          <a
            onClick={() => {
              if (card === null) {
                sign.sshow();
                return;
              }
              const f = card.find((elem) => elem.productId === product._id);
              if (f) {
                enqueueSnackbar("product exist in the card!");
                return;
              }

              dispatch(AddtoCard(product));
              sch();
              enqueueSnackbar("product added to card!", {
                variant: "success",
              });
            }}
          >
            Add To Card
          </a>
          <a>Subscribe</a>
          {/* <h4 className="price">${price}</h4>
          <select id="" name="price" defaultValue={"540"}>
            <option value="99">3 month </option>
            <option value="169">6 month </option>
            <option value="399">1 Year</option>
            <option value="540">All time </option>
          </select> */}
          <span>13 Activations</span>
          <span>43 Downloads</span>
        </form>
      </div>
      {sign.show ? <Signin close={sign.cshow} show={sign.show} /> : <></>}
    </>
  );
}

export default function Buy({ price, sch, product }) {
  return (
    <SnackbarProvider maxSnack={4}>
      <MyApp price={price} sch={sch} product={product} />
    </SnackbarProvider>
  );
}
