import React, { useEffect } from "react";
import { Link } from "react-router-dom";

export default function Face(props) {
  const handleClick = (e) => {
    if (document.getElementById("face")) {
      if (!document.getElementById("face").contains(e.target)) {
        document.removeEventListener("click", handleClick);
        props.close();
      }
    } else {
      document.removeEventListener("click", handleClick);
    }
  };

  useEffect(() => {
    document.addEventListener("click", handleClick);
    return () => {
      document.removeEventListener("click", handleClick);
      props.close();
    };
  });

  return (
    <>
      <div className="profile-nav" id="face">
        <ul>
          <li>
            <Link to="/profile">
              <img
                src="data:image/png;base64,"
                alt=""
              />
              <span>Profile</span>
            </Link>
          </li>
          <li>
            <Link to="/profile/products">My Products</Link>
          </li>
          <li>
            <Link to="/profile/notifications">Notifications</Link>
          </li>
          <li>
            <Link to="/profile/settings">Settings</Link>
          </li>
          <li>
            <a href="/">Log Out</a>
          </li>
        </ul>
      </div>
    </>
  );
}
