import React from "react";
import { Link } from "react-router-dom";
import Skeleton from "@material-ui/lab/Skeleton";
import { useSelector, useDispatch } from "react-redux";

import { AddtoCard } from "../../Actions";
import Show from "../show";
import Signin from "../signIn";
import { SnackbarProvider, useSnackbar } from "notistack";

function MyApp({ load, product, sch }) {
  const sign = Show();
  const { enqueueSnackbar } = useSnackbar();
  const dispatch = useDispatch();
  const card = useSelector((state) => {
    if (state.user === null) return null;
    const user = state.user;
    return user.card;
  });

  return (
    <>
      <div className="product-list">
        <div className="left-card">
          {load ? (
            <Skeleton variant="rect" className="skelton-me" />
          ) : (
            <img src={product.img} alt="" />
          )}
        </div>
        <div className="right-card">
          {load ? (
            <Skeleton variant="text" />
          ) : (
            <Link to={"/product/" + product._id}>
              <h2>{product.title}</h2>
            </Link>
          )}

          <div className="media-left-card">
            {load ? (
              <Skeleton variant="rect" className="skelton-me" />
            ) : (
              <img src={product.img} alt="" />
            )}
          </div>

          {load ? (
            <Skeleton variant="text" width="20%" />
          ) : (
            <h4>${product.price}</h4>
          )}
          {load ? (
            <Skeleton variant="rect" height={230} />
          ) : (
            <p>{product.description}</p>
          )}

          {load ? (
            <Skeleton variant="text" />
          ) : (
            <div className="btn-card">
              <a
                onClick={(e) => {
                  if (card === null) {
                    sign.sshow();
                    return;
                  }
                  const f = card.find((elem) => elem.productId === product._id);
                  if (f) {
                    enqueueSnackbar("product exist in the card!");
                    return;
                  }

                  dispatch(AddtoCard(product));
                  sch();
                  enqueueSnackbar("product added to card!", {
                    variant: "success",
                  });
                }}
              >
                Add To Card
              </a>
              <a href="/" className="buy-btn">
                Try Now
              </a>
            </div>
          )}
        </div>
      </div>

      {sign.show ? <Signin close={sign.cshow} show={sign.show} /> : <></>}
    </>
  );
}

export default function Productlist({ load, product, sch }) {
  return (
    <SnackbarProvider maxSnack={4}>
      <MyApp load={load} sch={sch} product={product} />
    </SnackbarProvider>
  );
}
